import React from 'react'

const Products = ({product : { product_name, brand_name, image, price, address, discription }}) => {
    return (
      <div className="box">
        <div className="upper">
          <img src={image} alt={product_name} />
          <ul>
            <li>{product_name}</li>
            <li>{brand_name}</li>
            <li>$ {price}</li>
          </ul>
        </div>

        <div className="lower">
          <p>
            Location: {address.state} <span>Date: 2021-12-11</span>
          </p>
          <p className='desc'>Description: {discription} </p>
        </div>
      </div>
    );
}

export default Products
