import React from 'react';
import CityBar from './CityBar';
import StateBar from './StateBar';
import ProductBar from './ProductBar';

const DropDown = () => {
  return (
    <>
      <div className="left">
        <p>filters</p>
        <div className="line"></div>
        <div className="drop">
          <ProductBar />
          <StateBar />

          <CityBar />
        </div>
      </div>
    </>
  );
};

export default DropDown;
